import styled from "styled-components";
import { NavLink } from "react-router-dom";
import { motion } from "framer-motion";
import { useLocation } from "react-router-dom";

function Nav() {
  const { pathname } = useLocation();
  return (
    <StyledNav>
      <h1>
        <NavLink id="logo" to="/">
          Capture
        </NavLink>
      </h1>
      <ul>
        <li>
          <NavLink  to="/about"  className={({ isActive }) => (isActive ? 'active' : 'inactive')}>1.About</NavLink>
          <Line
            transition={{ duration: 0.75 }}
            initial={{ width: "0%" }}
            animate={{ width: pathname === "/" ? "45%" : "0%" }}
          />
        </li>
        <li>
          <NavLink to="/work">2.Our Work</NavLink>
          <Line
            transition={{ duration: 0.75 }}
            initial={{ width: "0%" }}
            animate={{ width: pathname === "/work" ? "50%" : "0%" }}
          />
        </li>
        <li>
          <NavLink to="/contact">3.Contact Us</NavLink>
          <Line
            transition={{ duration: 0.75 }}
            initial={{ width: "0%" }}
            animate={{ width: pathname === "/contact" ? "50%" : "0%" }}
          />
        </li>
      </ul>
    </StyledNav>
  );
}

const StyledNav = styled.nav`
  min-height: 10vh;
  display: flex;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 10rem;
  background-color: #282828;
  position: sticky;
  top: 0;
  z-index: 10;
  a {
    color: white;
    text-decoration: none;
  }

  ul {
    display: flex;
    list-style: none;
  }
  #logo {
    font-size: 1.5rem;
    font-family: "Labster", cursive;
    font-weight: lighter;

  }
  li {
    padding-left: 10rem;
    position: relative;
  }
  @media(max-width:1300px){
    flex-direction: column;
    padding: 2rem 1rem ;
    #logo{
      display: inline-block;//a tag abe way lebkay ta margin warbgre
      padding: 1rem ;

    }
    }
    ul{
      padding: 2rem;
      justify-content: space-around;
      width: 100%;
    }
    li{
      padding: 0;
    }

`;

const Line = styled(motion.div)`
  height: 0.3rem;
  width: 0%;
  background-color: #23d997;
  position: absolute;
  bottom: -70%;
  left: 60%;
  @media (max-width: 1300px) {
    left: 0%;
  }
`;

export default Nav;
